import emitter from './emitter'
export default function (res, title = '') {
  if (res?.data?.success || res === true) {
    emitter.emit('push-message', {
      style: 'success',
      title: `${title}`
    })
  } else {
    const message = typeof res.data.message ===
        'string'
      ? [res.data.message]
      : res.data.message
    emitter.emit('push-message', {
      style: 'danger',
      title: `${title}`,
      content: message.join('')
    })
  }
}
